import React from 'react'
import { Table, Divider } from 'antd'
import LineChart from '@/common/components/BizCharts/LineChart'
import { postAction } from "../../../../axios";
import MainCard from "@/common/components/MainCard";
import {hostUrl} from "../../../../env"
import ReactDOM from "react-dom";

const columns = [{
    title: '序号',
    dataIndex: 'sortIndex',
    width: '10%',
}, {
    title: '月份',
    dataIndex: 'month',
    width: '10%',
}, {
    title: '家庭经济困难学生人均',
    dataIndex: 'student',
}, {
    title: '非家庭经济困难学生人均',
    dataIndex: 'noStudent',
}, {
    title: '全校学生人均',
    dataIndex: 'allSchool',
}];

export default class PoolStudentConsumption extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            tab: '1',
            chartIns: undefined,
            lineData: [
                // { month: "Jan", "家庭经济困难学生人均": 7.0, "非家庭经济困难学生人均": 3.9 }
            ],
            tableData: [
                // { key: '1', sortIndex:'1', college: '建筑学院', number: 165, rate: '5%' }
            ],
            pagination: {
                pageSize: 5,
                showQuickJumper: true,
                total: null,
                showTotal: (total) => {
                    return (
                        `共${total}条数据`
                    )
                }
            }
        }
        this.getData = this.getData.bind(this);
    }

    getData() {
        const { curYear, curCollege, curMajor, curGrade, curshengyuan } = this.props.filterValue
        postAction(
            "/bigdata/povertyAnalyze/consumeAvgContrast",
            {
                "schoolYear": curYear,
                "collegeCode": curCollege,
                "majorCode": curMajor,
                "grade": curGrade,
                "bornSource": curshengyuan
            }
        ).then((res) => {
            if (res.success) {
                let newData = [];
                let tableData = [];
                res.obj.forEach((item, index) => {
                    newData.push({
                        month: item.month + "月",
                        "家庭经济困难学生人均": item.povertyStu,
                        "非家庭经济困难学生人均": item.nopovertyStu,
                        "学生人均": item.allschoolStu
                    })
                    tableData.push({
                        key: (index + 1),
                        sortIndex: (index + 1),
                        month: item.month + "月",
                        student: item.povertyStu,
                        noStudent: item.nopovertyStu,
                        allSchool: item.allschoolStu
                    })
                })
                this.setState({
                    lineData: newData,
                    tableData,
                    pagination: {
                        pageSize: 5,
                        showQuickJumper: true,
                        total: tableData.length,
                        showTotal: (total) => {
                            return (
                                `共${total}条数据`
                            )
                        }
                    }
                })
            } else {
                this.setState({
                    lineData: [],
                    tableData: [],
                    pagination: {
                        pageSize: 5,
                        showQuickJumper: true,
                        total: null,
                        showTotal: (total) => {
                            return (
                                `共${total}条数据`
                            )
                        }
                    }
                })
            }
        })
    }

    //tab切换
    changeTab = (e) => {
        this.setState({ tab: e.target.value }, () => {
            if (this.refs['table']) {
                const tableCon = ReactDOM.findDOMNode(this.refs['table'])
                const table = tableCon.querySelector('table')
                table.setAttribute('id', 'consumption-xls')
            }
        })
    }

    //导出明细
    getDetail() {
        const { curYear, curCollege, curMajor, curGrade, curshengyuan } = this.props.filterValue;
        window.location.href = `${hostUrl}/bigdata/povertyAnalyze/exportConsumeAvgContrast?schoolYear=${curYear}&collegeCode=${curCollege}&majorCode=${curMajor}&grade=${curGrade}&bornSource=${curshengyuan}`;
    }

    render() {

        return (
            <MainCard
                className="main-card"
                title={<div className="common-title">人均月消费金额对比
                <span>（单位：元）</span>
                    <div className="filter-text" style={{ left: 0, top: 18 }}>{this.props.filterName.curYearName 
                        ? 
                        `${this.props.filterName.curYearName.substring(0,this.props.filterName.curYearName.length - 1)}学年-${Number((this.props.filterName.curYearName).substring(0,this.props.filterName.curYearName.length - 1)) + 1}学年`
                        : ""}
                        <Divider type="vertical" />{this.props.filterName.curCollegeName}
                        <Divider type="vertical" />{this.props.filterName.curMajorName}
                        <Divider type="vertical" />{this.props.filterName.curGradeName}
                        <Divider type="vertical" />{this.props.filterName.curshengyuanName}
                    </div>
                </div>}
                tab={this.state.tab}
                changeTab={this.changeTab}
                detailCallback={this.getDetail.bind(this)}
                downloadChart={{
                    chartIns: this.state.chartIns,
                    title: "人均月消费金额对比"
                }}
                tableToExcel={{
                    table: 'consumption-xls',
                    filename: '人均月消费金额对比excel下载'
                }}
            >
                {
                    this.state.tab === '1'
                        ?
                        <LineChart
                            data={this.state.lineData}
                            height={this.props.height - 80}
                            padding={[10, 30, 60, 80]}
                            colors={["#FACC14", "#1890FF", "#2FC25B"]}
                            onFirstLoad={(chartIns) => {
                                this.setState({ chartIns })
                            }}
                        />
                        :
                        <React.Fragment>
                            <Table
                                dataSource={this.state.tableData}
                                columns={columns}
                                bordered
                                size="small"
                                style={{ height: 225 }}
                                pagination={this.state.pagination}
                            />

                            <Table ref='table' columns={columns} dataSource={this.state.tableData} pagination={false} style={{ display: 'none' }} />
                        </React.Fragment>
                }
            </MainCard>
        )
    }

    componentDidMount() {
        this.getData()
    }
    componentWillReceiveProps(nextProps) {
        if (JSON.stringify(nextProps) !== JSON.stringify(this.props)) {
            setTimeout(() => {
                this.getData()
            }, 50)
        }
    }
}


