import React from 'react'
import { Table,Divider } from 'antd'
import Groupedcolumn from './GroupChart'
import { postAction } from "../../../../axios";
import MainCard from "@/common/components/MainCard";
import {hostUrl} from "../../../../env"
import ReactDOM from "react-dom";

const columns = [{
    title: '序号',
    dataIndex: 'sortIndex',
    width: '10%',
}, {
    title: '学期',
    dataIndex: 'item',
    render: (text) => {
        return (
            <span title={text}>{text}</span>
        )
    }
}, {
    title: '学霸',
    dataIndex: 'xueba'
},{
    title: '学优',
    dataIndex: 'xueyou'
},{
    title: '学中',
    dataIndex: 'xuezhong'
},{
    title: '学良',
    dataIndex: 'xueliang'
},{
    title: '学差',
    dataIndex: 'xuecha'
}];

export default class PoolStudentStudy extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            tab: '1',
            chartIns: undefined,
            pieData: [
                // { item: '事例一', count: 40 }
            ],
            tableData: [
                // { key: '1', sortIndex:'1', college: '建筑学院', number: 165, rate: '5%' }
            ],
            pagination: {
                pageSize: 5,
                showQuickJumper: true,
                total: null,
                showTotal: (total) => {
                    return (
                        `共${total}条数据`
                    )
                }
            }
        }
        this.getPieData = this.getPieData.bind(this);
    }

    getPieData() {
        const { curYear, curCollege, curMajor, curGrade, curshengyuan } = this.props.filterValue
        postAction(
            "/bigdata/povertyAnalyze/povertyStudentStudy",
            {
                "schoolYear": curYear,
                "collegeCode": curCollege,
                "majorCode": curMajor,
                "grade": curGrade,
                "bornSource": curshengyuan
            }
        ).then((res) => {
            if (res.success) {
                let newData = [];
                let tableData = [];
                res.obj.forEach((item, index) => {
                    newData.push({
                        name: item.semester,
                        "学霸": item.xueba,
                        "学优": item.xueyou,
                        "学中": item.xuezhong,
                        "学良": item.xueliang,
                        "学差": item.xuecha,
                    })

                    tableData.push({
                        key: (index + 1),
                        sortIndex: (index + 1),
                        item: item.semester,
                        xueba: item.xueba,
                        xueyou: item.xueyou,
                        xuezhong: item.xuezhong,
                        xueliang: item.xueliang,
                        xuecha:item.xuecha
                    })
                })
                this.setState({
                    pieData: newData,
                    tableData,
                    pagination: {
                        pageSize: 5,
                        showQuickJumper: true,
                        total: tableData.length,
                        showTotal: (total) => {
                            return (
                                `共${total}条数据`
                            )
                        }
                    }
                })
            } else {
                this.setState({
                    pieData: [],
                    tableData: [],
                    pagination: {
                        pageSize: 5,
                        showQuickJumper: true,
                        total: null,
                        showTotal: (total) => {
                            return (
                                `共${total}条数据`
                            )
                        }
                    }
                })
            }
        })
    }

    //tab切换
    changeTab = (e) => {
        this.setState({ tab: e.target.value }, () => {
            if (this.refs['table']) {
                const tableCon = ReactDOM.findDOMNode(this.refs['table'])
                const table = tableCon.querySelector('table')
                table.setAttribute('id', 'study-xls')
            }
        })
    }

    //导出明细
    getDetail() {
        const { curYear, curCollege, curMajor, curGrade, curshengyuan } = this.props.filterValue;
        window.location.href = `${hostUrl}/bigdata/povertyAnalyze/exportPovertyStudentStudy?schoolYear=${curYear}&collegeCode=${curCollege}&majorCode=${curMajor}&grade=${curGrade}&bornSource=${curshengyuan}`;
    }

    render() {
        return (
            <MainCard
                className="main-card"
                title={
                    <div className="common-title">家庭经济困难学生学习情况
                    <span>（单位：人）</span>
                        <div className="filter-text" style={{ left: 0, top: 18 }}>{this.props.filterName.curYearName 
                            ? 
                            `${this.props.filterName.curYearName.substring(0,this.props.filterName.curYearName.length - 1)}学年-${Number((this.props.filterName.curYearName).substring(0,this.props.filterName.curYearName.length - 1)) + 1}学年`
                            : ""}
                            <Divider type="vertical" />{this.props.filterName.curCollegeName}
                            <Divider type="vertical" />{this.props.filterName.curMajorName}
                            <Divider type="vertical" />{this.props.filterName.curGradeName}
                            <Divider type="vertical" />{this.props.filterName.curshengyuanName}
                        </div>
                    </div>}
                tab={this.state.tab}
                changeTab={this.changeTab}
                detailCallback={this.getDetail.bind(this)}
                downloadChart={{
                    chartIns: this.state.chartIns,
                    title: "家庭经济困难学生学习情况"
                }}
                tableToExcel={{
                    table: 'study-xls',
                    filename: '家庭经济困难学生学习情况excel下载'
                }}
            >
                {
                    this.state.tab === '1'
                        ?
                        <Groupedcolumn
                            data={this.state.pieData}
                            height={this.props.height - 80}
                            padding={[0, 20, 40, 20]}
                            colors={["#3aa1ff", "#5ddfcf", "#4ecb73"]}
                            onFirstLoad={(chartIns) => {
                                this.setState({ chartIns })
                            }}
                        />
                        :
                        <React.Fragment>
                            <Table
                                dataSource={this.state.tableData}
                                columns={columns}
                                bordered
                                size="small"
                                style={{ height: 320 }}
                                pagination={this.state.pagination}
                            />
                            <Table ref='table' columns={columns} dataSource={this.state.tableData} pagination={false} style={{ display: 'none' }} />
                        </React.Fragment>
                }
            </MainCard>

        )
    }

    componentDidMount() {
        this.getPieData()
    }

    componentWillReceiveProps(nextProps) {
        if (JSON.stringify(nextProps) !== JSON.stringify(this.props)) {
            setTimeout(() => {
                this.getPieData()
            }, 50)
        }
    }
}


