import React, {Component} from 'react';
import {Chart, Geom, Axis, Tooltip, Legend} from 'bizcharts';
// import DataSet from '@antv/data-set';
import { DataSet } from '@antv/data-set';
import Slider from "bizcharts-plugin-slider";
// const { DataView } = DataSet;
let ds;
let initEnd = 0;
export default class ShowChart extends Component {


    // 第一次加载完触发事件
    onFirstLoad(ev){
        if(this.props.onFirstLoad){
            this.props.onFirstLoad(ev);
        }
    }
    componentWillReceiveProps(nextProps){
		let data = nextProps.data;
		if(data && data[0]) {
			initEnd = Math.round(data.length / 2);
			setTimeout(
				() => {
					ds.setState("start", 0);
					ds.setState("end", initEnd);
				}
			)
		}
	}
    render () {

        const ages = ['金额达标率一级', '金额达标率二级', '金额达标率三级', '人数达标率一级', '人数达标率二级', '人数达标率三级'];
        let data=this.props.data
        // 分组图
        ds = new DataSet({
            state:{
                start: 0,
                end: data.length-1
            }
        }); // dateset数据集方法
        const dv = ds.createView().source(data); // 数据转换
        dv.source(data)
            .transform({
                type: 'fold',
                fields: ages,
                key: 'age',
                value: 'population',
                retains: [ 'State' ]
            }).transform({
				type: 'filter',
				callback: obj => {
			        const name = obj.State;
					let curIndex;
					this.props.data.forEach(
						(item, index) => {
							if (item.State === name) {
								curIndex = index
							}
						}
					)
					return curIndex <= ds.state.end && curIndex >= ds.state.start;
			    }
			})
            .transform({
                type: 'map',
                callback: obj => {
                    const key = obj.age;
                    let type;
                    if (key === '金额达标率一级' || key === '金额达标率二级' || key === '金额达标率三级') {
                        type = 'a';
                    } else if (key === '人数达标率一级' || key === '人数达标率二级' || key === '人数达标率三级') {
                        type = 'b';
                    }
                    obj.type = type;
                    return obj;
                }
            });
        const colorMap = {
            '金额达标率一级': '#1890FF',
            '金额达标率二级': '#13C2C2',
            '金额达标率三级': '#2FC25B',
            '人数达标率一级': '#3436C7',
            '人数达标率二级': '#8543E0',
            '人数达标率三级': '#223273',
        };
        const cols = {
            population: {
                tickInterval: 25
            }
        }

        return (
            <React.Fragment>
<Chart
                height={this.props.height}
                data={dv}
                scale={cols}
                padding={[10, 40, 60, 40]}
                onGetG2Instance={(ev)=>this.onFirstLoad(ev)}
                forceFit
            >
                <Axis name="population" label={{
                    formatter: function(val) {
                        return val;
                    }
                }} />
                <Legend position="bottom" />
                <Tooltip />
                <Geom type='interval' position='State*population' color={['age', function(age) {
                    return colorMap[age];
                }]} tooltip={['age*population', (age, population) => {
                    return {
                        name: age,
                        value: population + "%"
                    };
                }]}
                      adjust={[
                          {
                              type: 'dodge',
                              dodgeBy: 'type', // 按照 type 字段进行分组
                              marginRatio: 0 // 分组中各个柱子之间不留空隙
                          },
                          {
                              type: 'stack'
                          }
                      ]} />
            </Chart>
            <Slider
            width="auto"
            height={26}
            start={0}
            end={initEnd}
            xAxis={'State'}
            yAxis="population"
            onChange = {(obj) => {
                const { startValue, endValue } = obj;
                let _start, _end;
                this.props.data.forEach(
                    (item, index) => {
                        if(item.State === startValue) {
                            _start = index
                        }
                        if(item.State === endValue) {
                            _end = index
                        }
                    }
                )
                ds.setState("start", _start);
                ds.setState("end", _end);
            }}
            data={dv}
        />
            </React.Fragment>
            
        )
    }
}


