import React from 'react'
import { Table,Divider} from 'antd'
import PieChart from '@/common/components/BizCharts/PieChart'
import { postAction } from "../../../../axios";
import MainCard from "@/common/components/MainCard";
import ReactDOM from "react-dom";
import {hostUrl} from "../../../../env";


// import PieChart2 from './PieChart'
// const to = '南京'

const columns = [{
    title:'序号',
    dataIndex: 'sortIndex',
    width: '10%',
},{
    title: '民族',
    dataIndex: 'item',
    render: (text) => {
        return (
            <span title={text}>{text}</span>
        )
    }
}, {
    title: '人数',
    dataIndex: 'count'
}];

export default class PoolStudentSource extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
        tab: '1',
        chartIns: undefined,
      pieData: [
        // { item: '事例一', count: 40 }
      ],
        tableData: [
            // { key: '1', sortIndex:'1', college: '建筑学院', number: 165, rate: '5%' }
        ],
        pagination: {
            pageSize:5,
            showQuickJumper: true,
            total: null,
            showTotal: (total) => {
                return (
                    `共${total}条数据`
                )
            }
        }
    }
    this.getPieData = this.getPieData.bind(this);
  }

  

  getPieData() {
    const {curYear,curCollege,curMajor,curGrade,curshengyuan}=this.props.filterValue
    postAction(
      "/bigdata/povertyAnalyze/povertyStudentNational",
      {
        "schoolYear":curYear,
        "collegeCode":curCollege,
        "majorCode":curMajor,
        "grade":curGrade,
        "bornSource":curshengyuan
      }
    ).then((res) => {
      if (res.success) {
          let newData = [];
          let tableData = [];

        res.obj.forEach((item, index) => {

          newData.push({
            item: item.nation,
            count: item.num
          })

            tableData.push({
                key: (index + 1),
                sortIndex: (index + 1),
                item: item.nation,
                count: item.num,
            })
        })
        this.setState({
          pieData: newData,
            tableData,
            pagination: {
                pageSize: 5,
                showQuickJumper: true,
                total: tableData.length,
                showTotal: (total) => {
                    return (
                        `共${total}条数据`
                    )
                }
            }
        })
      } else {
        this.setState({
          pieData: [],
            tableData: [],
            pagination: {
                pageSize: 5,
                showQuickJumper: true,
                total: null,
                showTotal: (total) => {
                    return (
                        `共${total}条数据`
                    )
                }
            }
        })
      }
    })
  }

    //导出明细
    getDetail () {
        const {curYear,curCollege,curMajor,curGrade,curshengyuan} = this.props.filterValue;
        window.location.href=`${hostUrl}/bigdata/povertyAnalyze/exportPovertyStudentNational?schoolYear=${curYear}&collegeCode=${curCollege}&majorCode=${curMajor}&grade=${curGrade}&bornSource=${curshengyuan}`;
    }

    //tab切换
    changeTab = (e) => {
        this.setState({ tab: e.target.value }, () => {
            if (this.refs['table']) {
                const tableCon = ReactDOM.findDOMNode(this.refs['table'])
                const table = tableCon.querySelector('table')
                table.setAttribute('id', 'national-xls')
            }
        })
    }


  render() {
    // console.log(this.state.pieData)
    return (

        <MainCard
            className="main-card"
            title={<div className="common-title">家庭经济困难学生民族分布TOP5
                <span>（单位：人）</span>
                <div className="filter-text" style={{top:20,left:0}}>{this.props.filterName.curYearName 
                  ?
                  `${this.props.filterName.curYearName.substring(0,this.props.filterName.curYearName.length - 1)}学年-${Number((this.props.filterName.curYearName).substring(0,this.props.filterName.curYearName.length - 1)) + 1}学年`
                   : ""}
                    <Divider type="vertical" />{this.props.filterName.curCollegeName}
                    <Divider type="vertical" />{this.props.filterName.curMajorName}
                    <Divider type="vertical" />{this.props.filterName.curGradeName}
                    <Divider type="vertical" />{this.props.filterName.curshengyuanName}
                </div>
            </div>}
            tab={this.state.tab}
            changeTab={this.changeTab}
            detailCallback={this.getDetail.bind(this)}
            downloadChart={{
                chartIns: this.state.chartIns,
                title: "家庭经济困难学生民族分布TOP5"
            }}
            tableToExcel={{
                table: 'national-xls',
                filename: '家庭经济困难学生民族分布TOP5excel下载'
            }}
        >
            {
                this.state.tab === '1'
                    ?
            <PieChart
              data={this.state.pieData}
              innerRadius={0.55}
              height={this.props.height - 85}
              padding={[0, 20, 40, 20]}
              colors = {["#3aa1ff","#5ddfcf","#4ecb73","#fbd437","#eaa674","#8a7bd4","#975fe5","#749fea","#36cbcb","#74b1ea","#5254cf","#435188"]}
              onFirstLoad={(chartIns) => {
                  this.setState({ chartIns })
              }}
            />
                    :
                    <React.Fragment>
                        <Table
                            dataSource={this.state.tableData}
                            columns={columns}
                            bordered
                            size="small"
                            style={{height:225}}
                            pagination={this.state.pagination}
                        />

                        <Table ref='table' columns={columns} dataSource={this.state.tableData} pagination={false} style={{ display: 'none' }} />
                    </React.Fragment>
            }
        </MainCard>
       
    )
  }

  // componentDidMount() {
  //   this.getData()
  //   this.getPieData()
  // }

  componentWillReceiveProps(nextProps) {
    if (JSON.stringify(nextProps) !== JSON.stringify(this.props)) {
      setTimeout(() => {
        this.getPieData()
      }, 50)
    }
  }
}


