import React from "react";
import { Chart, Geom, Axis, Tooltip, Legend } from "bizcharts";
import DataSet from "@antv/data-set";
import NoData from '../../../../common/components/NoData/NoDataComponent'

class Groupedcolumn extends React.Component {

   // 第一次加载完触发事件
   onFirstLoad(ev){
    if(this.props.onFirstLoad){
        this.props.onFirstLoad(ev);
    }
}

  render() {

    const height = this.props.height || 350; // 高度设置，默认值

    const ds = new DataSet();
    const dv = ds.createView().source(this.props.data);
    dv.transform({
      type: "fold",
      fields: ["学霸", "学优", "学良", "学中", "学差"],
      key: "段位",
      value: "平均成绩"
    });
    return (
      <div>
        {
          this.props.data && this.props.data.length > 0
            ?
            <Chart height={this.props.height} data={dv} forceFit
                onGetG2Instance={(ev)=>this.onFirstLoad(ev)} >
              <Axis name="段位" />
              <Axis name="平均成绩" />
              <Legend />
              <Tooltip
                crosshairs={{
                  type: "y"
                }}
              />
              <Geom
                type="interval"
                position="段位*平均成绩"
                color={"name"}
                adjust={[
                  {
                    type: "dodge",
                    marginRatio: 1 / 32
                  }
                ]}
              />
            </Chart>
            :
            <NoData data={'nodata'} height={height} />
        }
      </div>
    );
  }
}

export default Groupedcolumn;


