import React from 'react';
import {Row,Col} from 'antd'
import PoorStudents from './PoorStudents/index'
import DiffLevelNum from './DiffLevelNum/index'
import ZiZhuDaBiao from './ZiZhuDaBiao/index'
import './style.less';
import '../index.less';
import Filter from './Filter'
import Renjunzhi from './renjunzhi'
import Dabiaolv from './dabiaolv'
import Xuexi from './xuexi'
import JiuyeTop5 from './jiuyeTop5'
import Xueshengfenbu from './xueshengfenbu'
import Shengyuandi from './shengyuandi'
import MinzuTop5 from './minzuTop5'
import Xiaofei from  './xiaofei'



class Pinkunshengfenxi extends React.Component {
	constructor(props){
		super(props)
		this.state={
			filterValue: {},
      		filterName: {},
		}
		this.getFilterValueAndName = this.getFilterValueAndName.bind(this)
	}
	// 获取筛选条件中的value用请求， 获取筛选条件的name用于标注生效条件
	getFilterValueAndName(a, b) {
		this.setState({
			filterValue: a,
			filterName: b
		})
	}


	render() {
		const {filterName,filterValue} = this.state;

		return (
			<div>
				<Filter getFilterValueAndName = {this.getFilterValueAndName.bind(this)}/>
				<div className="pinkunshengfenxi" style={{marginTop:20}}>
					<Row>
						<Col span={12} style={{paddingRight:10}}>
							{/* 家庭经济困难学生分析 */}
							<PoorStudents filterName = {filterName} filterValue = { filterValue } height={300}/>
						</Col>
						<Col span={12} style={{paddingLeft:10}}>
							{/* 不同级别人数 */}
							<DiffLevelNum filterName = {filterName} filterValue = { filterValue } height={300}/>
						</Col>
					</Row>
					<Row  gutter={20} style={{marginTop:20}}>
						<Col span={12}>
                            {/* 不同级别人均值 */}
							<Renjunzhi  filterName = {filterName} filterValue = { filterValue } height={350} />
						</Col>
						<Col span={12}>
                            {/* 资助达标率 */}
							<Dabiaolv   filterName = {filterName} filterValue = { filterValue } height={350}/>
						</Col>
					</Row>
					<Row>
						<Col span={24} style={{marginTop:20}}>
                            {/* 各学院资助达标率分布 */}
							<ZiZhuDaBiao height={350} filterName = {filterName} filterValue = { filterValue }/>
						</Col>
					</Row>
					<Row  gutter={20} style={{marginTop:20}}>
						<Col span={12}>
                            {/* 家庭经济困难学生学习情况 */}
						<Xuexi  filterName = {filterName} filterValue = { filterValue } height={400}/>
						</Col>
						<Col span={12}>
                            {/* 家庭经济困难学生就业去向TOP5 */}
						<JiuyeTop5  filterName = {filterName} filterValue = { filterValue } height={400}/>
						</Col>
					</Row>
					<Row  style={{marginTop:20}}>
						<Col span={24}>
                            {/* 各学院家庭经济困难学生分布 */}
							<Xueshengfenbu  filterName = {filterName} filterValue = { filterValue } height={400} />
						</Col>
					</Row>
					<Row  gutter={20} style={{marginTop:20}}>
						<Col span={12}>
                            {/* 家庭经济困难学生生源地分布 */}
						<Shengyuandi  filterName = {filterName} filterValue = { filterValue } height={400} />
						</Col>
						<Col span={12}>
                            {/* 家庭经济困难学生民族分布TOP5 */}
						<MinzuTop5  filterName = {filterName} filterValue = { filterValue } height={400} />
						</Col>
					</Row>
					<Row style={{marginTop:20}}>
						<Col span={24}>
                            {/* 人均月消费金额对比 */}
						<Xiaofei  filterName = {filterName} filterValue = { filterValue } height={350} />
						</Col>
					</Row>					
					
					
				</div>
			</div>
			
		)
	}
}

export default Pinkunshengfenxi



