import React from 'react'
import ReactDOM from 'react-dom';
import BarChart3 from './BarChart3'
import { getAction } from "../../../../axios";
import MainCard from "@/common/components/MainCard";
import { Table } from 'antd'
import { hostUrl } from "../../../../env";

const columns = [{
  title: '序号',
  dataIndex: 'sortIndex',
  width: '10%',
}, {
  title: '类型',
  dataIndex: 'type',
  render: (text) => {
    return (
      <span title={text}>{text}</span>
    )
  }
}, {
  title: '人数',
  dataIndex: 'number'
}];
export default class PoorStudents extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      tab: '1',
      chartIns: undefined,
      chartData: [
        // { name: '计算机院', "人数": 117 }
      ],
      tableData: [
        // { key: '1', sortIndex:'1', college: '建筑学院', number: 165, rate: '5%' }
      ],
      total: 0,
      pagination: {
        pageSize: 5,
        showQuickJumper: true,
        total: null,
        showTotal: (total) => {
          return (
            `共${total}条数据`
          )
        }
      }
    }
  }

  getData() {
    getAction(
      "/bigdata/povertyAnalyze/povertyAnalyzeByGroup"
    ).then((res) => {
      if (res.success) {
        let newData = [];
        let tableData = []
        res.obj.forEach((item, index) => {
          newData.push({
            label: item.type,
            '人数': item.total
          })
          tableData.push({
            key: (index + 1),
            sortIndex: (index + 1),
            type: item.type,
            number: item.total,
          })
        })
        this.setState({
          chartData: newData,
          tableData,
          pagination: {
            pageSize: 5,
            showQuickJumper: true,
            total: tableData.length,
            showTotal: (total) => {
              return (
                `共${total}条数据`
              )
            }
          }
        })
      } else {
        this.setState({
          chartData: [],
          tableData: [],
          pagination: {
            pageSize: 5,
            showQuickJumper: true,
            total: null,
            showTotal: (total) => {
              return (
                `共${total}条数据`
              )
            }
          }
        })
      }
    })
  }
  //tab切换
  changeTab = (e) => {
    this.setState({ tab: e.target.value }, () => {
      if (this.refs['table']) {
        const tableCon = ReactDOM.findDOMNode(this.refs['table'])
        const table = tableCon.querySelector('table')
        table.setAttribute('id', 'student-xls')
      }
    })
  }

  //导出明细
    getDetail () {
        window.location.href=`${hostUrl}/bigdata/povertyAnalyze/exportPovertyAnalyzeByGroup`;
    }

  render() {
    return (
      <MainCard
        title={<div className="common-title">家庭经济困难学生分析
                <span>（单位：人）</span>
                <div className="filter-text" style={{top:20,left:0}}>当前学年（不受筛选条件控制）</div>
              </div>}
        tab={this.state.tab}
        changeTab={this.changeTab}
        detailCallback={this.getDetail.bind(this)}
        downloadChart={{
          chartIns: this.state.chartIns,
          title: "家庭经济困难学生分析"
        }}
        tableToExcel={{
          table: 'student-xls',
          filename: '家庭经济困难学生分析excel下载'
        }}
      >

        {
          this.state.tab === '1'
            ?
            <BarChart3
              data={this.state.chartData}
              transpose
              noLegend={true}
              geomSize={30}
              padding={[0, 40, 30, 160]}
              height={this.props.height - 70}
              onFirstLoad={(chartIns) => { this.setState({ chartIns }) }}
              colors={["#3aa1ff", "#5ddfcf", "#4ecb73", "#fbd437", "#eaa674", "#8a7bd4", "#975fe5", "#749fea", "#36cbcb", "#74b1ea", "#5254cf", "#435188"]}
            />
            :
            <React.Fragment>
              <Table
                dataSource={this.state.tableData}
                columns={columns}
                bordered
                size="small"
                style={{ height: 225 }}
                pagination={this.state.pagination}
              />

              <Table ref='table' columns={columns} dataSource={this.state.tableData} pagination={false} style={{ display: 'none' }} />
            </React.Fragment>
        }

      </MainCard>
    )
  }

  componentDidMount() {
    this.getData()
  }

  componentWillReceiveProps(nextProps) {
    if (JSON.stringify(nextProps) !== JSON.stringify(this.props)) {
      setTimeout(() => {
        this.getData()
      }, 50)
    }
  }
}


