import React from 'react'
import { Table,Divider} from 'antd'
import {postAction} from "../../../../axios";
import Barchart from '@/common/components/BizCharts/BarChart'
import MainCard from "@/common/components/MainCard";
import ReactDOM from "react-dom";
import {hostUrl} from "../../../../env";


const columns = [{
    title:'序号',
    dataIndex: 'sortIndex',
    width: '10%',
},{
    title: '不同级别',
    dataIndex: 'item',
    render: (text) => {
        return (
            <span title={text}>{text}</span>
        )
    }
}, {
    title: '金额(%)',
    dataIndex: 'money'
}, {
    title: '人数(%)',
    dataIndex: 'count'
}];

export default class PoolStudentMoney extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
        tab: '1',
        chartIns: undefined,
        data: [
            // {
            //     label: "一级家庭经济困难学生",
            //     '金额':100,
            //     '人数': 10
            // }
        ],
        tableData: [
            // { key: '1', sortIndex:'1', college: '建筑学院', number: 165, rate: '5%' }
        ],
        pagination: {
            pageSize:5,
            showQuickJumper: true,
            total: null,
            showTotal: (total) => {
                return (
                    `共${total}条数据`
                )
            }
        }
    }
    this.getData = this.getData.bind(this);
  }

  

  getData () {
    const {curYear,curCollege,curMajor,curGrade,curshengyuan} = this.props.filterValue
    postAction(
        "/bigdata/povertyAnalyze/subsidizePercent",
        {
            "schoolYear":curYear,
            "collegeCode":curCollege,
            "majorCode":curMajor,
            "grade":curGrade,
            "bornSource":curshengyuan
        }
    ).then((res) => {
      if (res.success) {
          let newData = [];
          let tableData = [];

        res.obj.forEach((item, index) => {

          newData.push({
              label: item.analyzeGroup,
              '金额': Number(item.moneyPercent),
              '人数': Number(item.numPercent)
          })

            tableData.push({
                key: (index + 1),
                sortIndex: (index + 1),
                item: item.analyzeGroup,
                money:item.moneyPercent,
                count: item.numPercent,
            })
        })
          this.setState({
              data: newData,
              tableData,
              pagination: {
                  pageSize: 5,
                  showQuickJumper: true,
                  total: tableData.length,
                  showTotal: (total) => {
                      return (
                          `共${total}条数据`
                      )
                  }
              }
          })
      } else {
        this.setState({
            data: [],
            tableData: [],
            pagination: {
                pageSize: 5,
                showQuickJumper: true,
                total: null,
                showTotal: (total) => {
                    return (
                        `共${total}条数据`
                    )
                }
            }
        })
      }
    })
  }

    //导出明细
    getDetail () {
        const {curYear,curCollege,curMajor,curGrade,curshengyuan} = this.props.filterValue;
        window.location.href=`${hostUrl}/bigdata/povertyAnalyze/exportSubsidizePercent?schoolYear=${curYear}&collegeCode=${curCollege}&majorCode=${curMajor}&grade=${curGrade}&bornSource=${curshengyuan}`;
    }

    //tab切换
    changeTab = (e) => {
        this.setState({ tab: e.target.value }, () => {
            if (this.refs['table']) {
                const tableCon = ReactDOM.findDOMNode(this.refs['table'])
                const table = tableCon.querySelector('table')
                table.setAttribute('id', 'money-xls')
            }
        })
    }

  render() {
    return (
        <MainCard
            className="main-card"
            title={<div className="common-title">资助达标率
                <span>（单位：%）</span>
                <div className="filter-text" style={{top:20,left:0}}>{this.props.filterName.curYearName 
                    ? 
                    `${this.props.filterName.curYearName.substring(0,this.props.filterName.curYearName.length - 1)}学年-${Number((this.props.filterName.curYearName).substring(0,this.props.filterName.curYearName.length - 1)) + 1}学年`
                    : ""}
                    <Divider type="vertical" />{this.props.filterName.curCollegeName}
                    <Divider type="vertical" />{this.props.filterName.curMajorName}
                    <Divider type="vertical" />{this.props.filterName.curGradeName}
                    <Divider type="vertical" />{this.props.filterName.curshengyuanName}
                </div>
            </div>}
            tab={this.state.tab}
            changeTab={this.changeTab}
            detailCallback={this.getDetail.bind(this)}
            downloadChart={{
                chartIns: this.state.chartIns,
                title: "资助达标率"
            }}
            tableToExcel={{
                table: 'money-xls',
                filename: '资助达标率excel下载'
            }}
        >
            {
                this.state.tab === '1'
                    ?
        <Barchart
            data={this.state.data}
            transpose
            height={this.props.height - 80}
            padding={[10,40,60,80]}
            colors={["#1890ff","#5ddfcf"]}
            geomTooltip={['type*value', (type, item)=>{
                return {
                    name:type,
                    value:item + '%'
                }
            }]}
            onFirstLoad={(chartIns) => {
                this.setState({ chartIns })
            }}
        />
                    :
                    <React.Fragment>
                        <Table
                            dataSource={this.state.tableData}
                            columns={columns}
                            bordered
                            size="small"
                            style={{height:225}}
                            pagination={this.state.pagination}
                        />

                        <Table ref='table' columns={columns} dataSource={this.state.tableData} pagination={false} style={{ display: 'none' }} />
                    </React.Fragment>
            }
        </MainCard>
        
    )
  }

  componentWillReceiveProps (nextProps) {
    if (JSON.stringify(nextProps) !== JSON.stringify(this.props)){
      setTimeout(() => {
          this.getData()
      }, 50)
    }
  }
}


