import React from 'react'
import {Divider, Table} from 'antd'
import BarChart4 from './BarChart4'
import {postAction} from "../../../../axios";
import MainCard from "@/common/components/MainCard";
import ReactDOM from "react-dom";
import {hostUrl} from "../../../../env";


const columns = [{
    title:'序号',
    dataIndex: 'sortIndex',
    width: '10%',
},{
    title: '不同级别',
    dataIndex: 'item',
    render: (text) => {
        return (
            <span title={text}>{text}</span>
        )
    }
}, {
    title: '人均值',
    dataIndex: 'count'
}];
export default class PoolStudentMoney extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
        tab: '1',
        chartIns: undefined,
        barData: [
            // {
            //     label: "一级家庭经济困难学生",
            //     '金额': 10
            // }
        ],
        tableData: [
            // { key: '1', sortIndex:'1', college: '建筑学院', number: 165, rate: '5%' }
        ],
        pagination: {
            pageSize:5,
            showQuickJumper: true,
            total: null,
            showTotal: (total) => {
                return (
                    `共${total}条数据`
                )
            }
        }
    }
    this.getBarData = this.getBarData.bind(this);
  }

  getBarData () {
    const {curYear,curCollege,curMajor,curGrade,curshengyuan}=this.props.filterValue    
    postAction(
        "/bigdata/povertyAnalyze/povertyLevelSubsidizeAvg",{
          "schoolYear":curYear,
          "collegeCode":curCollege,
          "majorCode":curMajor,
          "grade":curGrade,
          "bornSource":curshengyuan
        }
    ).then((res) => {
      if (res.success) {
          let newData = [];
          let tableData = [];
        res.obj.forEach((item, index) => {
          newData.push({
              label: item.analyzeGroup,
              '金额': Number(item.analyzeValue)
          })
            tableData.push({
                key: (index + 1),
                sortIndex: (index + 1),
                item: item.analyzeGroup,
                count: item.analyzeValue,
            })
        })
          this.setState({
              barData: newData,
              tableData,
              pagination: {
                  pageSize: 5,
                  showQuickJumper: true,
                  total: tableData.length,
                  showTotal: (total) => {
                      return (
                          `共${total}条数据`
                      )
                  }
              }
          })
      } else {
        this.setState({
            barData: [],
            tableData: [],
            pagination: {
                pageSize: 5,
                showQuickJumper: true,
                total: null,
                showTotal: (total) => {
                    return (
                        `共${total}条数据`
                    )
                }
            }
        })
      }
    })
  }

    //导出明细
    getDetail () {
        const {curYear,curCollege,curMajor,curGrade,curshengyuan} = this.props.filterValue;
        window.location.href=`${hostUrl}/bigdata/povertyAnalyze/exportPovertyLevelSubsidizeAvg?schoolYear=${curYear}&collegeCode=${curCollege}&majorCode=${curMajor}&grade=${curGrade}&bornSource=${curshengyuan}`;
    }

    //tab切换
    changeTab = (e) => {
        this.setState({ tab: e.target.value }, () => {
            if (this.refs['table']) {
                const tableCon = ReactDOM.findDOMNode(this.refs['table'])
                const table = tableCon.querySelector('table')
                table.setAttribute('id', 'capita-xls')
            }
        })
    }



    render() {
    return (
        <MainCard
            className="main-card"
            title={<div className="common-title">不同级别人均值
                <span>（单位：元）</span>
                <div className="filter-text" style={{top:20,left:0}}>{this.props.filterName.curYearName 
                    ? 
                    `${this.props.filterName.curYearName.substring(0,this.props.filterName.curYearName.length - 1)}学年-${Number((this.props.filterName.curYearName).substring(0,this.props.filterName.curYearName.length - 1)) + 1}学年`
                    : ""}
                    <Divider type="vertical" />{this.props.filterName.curCollegeName}
                    <Divider type="vertical" />{this.props.filterName.curMajorName}
                    <Divider type="vertical" />{this.props.filterName.curGradeName}
                    <Divider type="vertical" />{this.props.filterName.curshengyuanName}
                </div>
            </div>}
            tab={this.state.tab}
            changeTab={this.changeTab}
            detailCallback={this.getDetail.bind(this)}
            downloadChart={{
                chartIns: this.state.chartIns,
                title: "不同级别人均值"
            }}
            tableToExcel={{
                table: 'capita-xls',
                filename: '不同级别人均值excel下载'
            }}
        >
            {
                this.state.tab === '1'
                    ?
            <BarChart4
              data={this.state.barData}
              noLegend={true}
              geomSize={50}
              colors = {["#3aa1ff","#5ddfcf","#4ecb73","#fbd437","#eaa674","#8a7bd4","#975fe5","#749fea","#36cbcb","#74b1ea","#5254cf","#435188"]}
              height={this.props.height - 80}
              padding={[10,40,40,80]}
              onFirstLoad={(chartIns) => {
                  this.setState({ chartIns })
              }}
            />
                    :
                    <React.Fragment>
                        <Table
                            dataSource={this.state.tableData}
                            columns={columns}
                            bordered
                            size="small"
                            style={{height:225}}
                            pagination={this.state.pagination}
                        />

                        <Table ref='table' columns={columns} dataSource={this.state.tableData} pagination={false} style={{ display: 'none' }} />
                    </React.Fragment>
            }
          </MainCard>
      
    )
  }

  // componentDidMount () {
  //   this.getBarData()
  //   this.getData()
  // }

  componentWillReceiveProps (nextProps) {
    if (JSON.stringify(nextProps) !== JSON.stringify(this.props)){
      setTimeout(() => {
          this.getBarData()
      }, 50)
    }
  }
}


