import React from 'react'
import ReactDOM from 'react-dom';
import { Table } from 'antd'
import MainCard from "@/common/components/MainCard";
// import BarChart from '../../../common/components/BizCharts/BarChart'
import { postAction } from "../../../../axios";
import BarChart from "./ShowChart";
import { hostUrl } from "../../../../env"

const columns = [{
  title: '序号',
  dataIndex: 'sortIndex',
  width: '10%',
}, {
  title: '学院',
  dataIndex: 'type',
  render: (text) => {
    return (
      <span title={text}>{text}</span>
    )
  }
}, {
  title: '一级',
  dataIndex: 'one'
}
  , {
  title: '二级',
  dataIndex: 'two'
},
{
  title: '三级',
  dataIndex: 'three'
}];
export default class PoolStudentDistribution extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      barData: [
        // {
        //     label: "学院一",
        //     "一级贫困": 2,
        //     "二级贫困": 6,
        //     "三级贫困": 12
        // }
      ],
      tab: '1',
      chartIns: undefined,
      tableData: [
        // { key: '1', sortIndex:'1', college: '建筑学院', number: 165, rate: '5%' }
      ],
      total: 0,
      pagination: {
        showQuickJumper: true,
        total: null,
        showTotal: (total) => {
          return (
            `共${total}条数据`
          )
        }
      }
    }
    this.getData = this.getData.bind(this);
  }

  getData() {
    const { curYear } = this.props.filterValue
    postAction(
      "/bigdata/povertyAnalyze/povertyStudentCollege",
      {
        "schoolYear": curYear
      }
    ).then((res) => {
      if (res.success) {
        let newData = [];
        let tableData = []
        res.obj.forEach((item, index) => {
          newData.push({
            name: item.name,
            "一级": item.oneLevel,
            "二级": item.twoLevel,
            "三级": item.threeLevel,
            total: item.total || 0
          })
          tableData.push({
            key: (index + 1),
            sortIndex: (index + 1),
            type: item.name,
            one: item.oneLevel,
            two: item.twoLevel,
            three: item.threeLevel
            // number: item.total
          })
        })
        this.setState({
          barData: newData,
          tableData
        })
      } else {
        this.setState({
          barData: [],
          tableData: []
        })
      }
    })
  }
  //tab切换
  changeTab = (e) => {
    this.setState({ tab: e.target.value }, () => {
      if (this.refs['table']) {
        const tableCon = ReactDOM.findDOMNode(this.refs['table'])
        const table = tableCon.querySelector('table')
        table.setAttribute('id', 'student-xls')
      }
    })
  }

  //导出明细
  getDetail() {
    const { curYear, curCollege, curMajor, curGrade, curshengyuan } = this.props.filterValue;
    window.location.href = `${hostUrl}/bigdata/povertyAnalyze/exportPovertyCollegeDetail?schoolYear=${curYear}&collegeCode=${curCollege}&majorCode=${curMajor}&grade=${curGrade}&bornSource=${curshengyuan}`;
  }

  render() {

    return (
      <MainCard
        title={<div className="common-title">各学院家庭经济困难学生分布<span>（单位：人）</span>
          <div className="filter-text-sp">{this.props.filterName.curYearName
            ? 
            `${this.props.filterName.curYearName.substring(0,this.props.filterName.curYearName.length - 1)}学年-${Number((this.props.filterName.curYearName).substring(0,this.props.filterName.curYearName.length - 1)) + 1}学年`
            : ""}
          </div></div>}
        tab={this.state.tab}
        changeTab={this.changeTab}
        detailCallback={this.getDetail.bind(this)}
        downloadChart={{
          chartIns: this.state.chartIns,
          title: "各学院家庭经济困难学生分布"
        }}
        tableToExcel={{
          table: 'student-xls',
          filename: '各学院家庭经济困难学生分布excel下载'
        }}
      >
        {
          this.state.tab === '1'
            ?
            <BarChart
              data={this.state.barData}
              geomType={'intervalStack'}
              height={this.props.height - 80}
              padding={[10, 40, 30, 50]}
              onFirstLoad={(chartIns) => { this.setState({ chartIns }) }}
              // colors={["#1890ff","#2FC25B","#f5a623"]}
              colors={["#3aa1ff", "#5ddfcf", "#4ecb73", "#fbd437", "#eaa674", "#8a7bd4", "#975fe5", "#749fea", "#36cbcb", "#74b1ea", "#5254cf", "#435188"]}
            />
            :
            <React.Fragment>
              <Table
                dataSource={this.state.tableData}
                columns={columns}
                bordered
                size="middle"
              // pagination={this.state.pagination}
              />

              <Table ref='table' columns={columns} dataSource={this.state.tableData} pagination={false} style={{ display: 'none' }} />
            </React.Fragment>
        }

      </MainCard>
    )
  }

  // componentDidMount () {
  //   this.getData()
  // }

  componentWillReceiveProps(nextProps) {
    if (JSON.stringify(nextProps) !== JSON.stringify(this.props)) {
      setTimeout(() => {
        this.getData()
      }, 50)
    }
  }
}


