import React from 'react'
import ReactDOM from 'react-dom'
import { Table, Divider } from 'antd'
import MainCard from "@/common/components/MainCard"
import { postAction } from "../../../../axios";
import BarChart from './ShowChart'
import { hostUrl } from "../../../../env"

const columns = [{
  title: '序号',
  dataIndex: 'sortIndex',
  width: '10%',
}, {
  title: '生源地',
  dataIndex: 'address',
  render: (text) => {
    return (
      <span title={text}>{text}</span>
    )
  }
}, {
  title: '一级',
  dataIndex: 'one'
}
  , {
  title: '二级',
  dataIndex: 'two'
},
{
  title: '三级',
  dataIndex: 'three'
},
{
  title: '总数',
  dataIndex: 'total'
}];
export default class PoolStudentSource extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      tab: '1',
      chartIns: undefined,
      tableData: [
        // { key: '1', sortIndex:'1', college: '建筑学院', number: 165, rate: '5%' }
      ],
      total: 0,
      pagination: {
        pageSize:5,
        showQuickJumper: true,
        total: null,
        showTotal: (total) => {
          return (
            `共${total}条数据`
          )
        }
      },
      datas: [
        // { from: '湖北', count: 354552, to: to }
      ]
    }
    this.getData = this.getData.bind(this);
  }

  getData() {
    const { curYear, curCollege, curMajor, curGrade, curshengyuan } = this.props.filterValue
    postAction(
      "/bigdata/povertyAnalyze/povertyStudentSource",
      {
        "schoolYear": curYear,
        "collegeCode": curCollege,
        "majorCode": curMajor,
        "grade": curGrade,
        "bornSource": curshengyuan
      }
    ).then((res) => {
      if (res.success) {
        let _chartData = [], tableData = [];
        res.obj.forEach(
          (item, index) => {
            _chartData.push({
              name: item.bornSource,
              total: item.total,
              "一级": item.oneLevel,
              "二级": item.twoLevel,
              "三级": item.threeLevel,
            })
            tableData.push({
              key: (index + 1),
              sortIndex: (index + 1),
              address: item.bornSource,
              one: item.oneLevel,
              two: item.twoLevel,
              three: item.threeLevel,
              total: item.total
            })
          }
        )
        this.setState({
          datas: _chartData,
          tableData,
          pagination: {
            pageSize:5,
            showQuickJumper: true,
            total: tableData.length,
            showTotal: (total) => {
              return (
                `共${total}条数据`
              )
            }
          }
        })
      } else {
        this.setState({
          datas: [],
          tableData:[]
        })
      }
    })
  }

  //tab切换
  changeTab = (e) => {
    this.setState({ tab: e.target.value }, () => {
      if (this.refs['table']) {
        const tableCon = ReactDOM.findDOMNode(this.refs['table'])
        const table = tableCon.querySelector('table')
        table.setAttribute('id', 'shenyuan-xls')
      }
    })
  }

  //导出明细
  getDetail() {
    const { curYear, curCollege, curMajor, curGrade, curshengyuan } = this.props.filterValue;
    window.location.href = `${hostUrl}/bigdata/povertyAnalyze/exportPovertyStudentSource?schoolYear=${curYear}&collegeCode=${curCollege}&majorCode=${curMajor}&grade=${curGrade}&bornSource=${curshengyuan}`;
  }

  render() {
    return (

      <MainCard
        title={<div className="common-title">家庭经济困难学生生源地分布<span>（单位：人）</span>
          <div className="filter-text" style={{top:18,left:0}}>{this.props.filterName.curYearName 
          ?
          `${this.props.filterName.curYearName.substring(0,this.props.filterName.curYearName.length - 1)}学年-${Number((this.props.filterName.curYearName).substring(0,this.props.filterName.curYearName.length - 1)) + 1}学年`
           : ""}
            <Divider type="vertical" />{this.props.filterName.curCollegeName}
            <Divider type="vertical" />{this.props.filterName.curMajorName}
            <Divider type="vertical" />{this.props.filterName.curGradeName}
            <Divider type="vertical" />{this.props.filterName.curshengyuanName}
          </div>
          </div>}
        tab={this.state.tab}
        changeTab={this.changeTab}
        detailCallback={this.getDetail.bind(this)}
        downloadChart={{
          chartIns: this.state.chartIns,
          title: "家庭经济困难学生生源地分布"
        }}
        tableToExcel={{
          table: 'shenyuan-xls',
          filename: '家庭经济困难学生生源地分布excel下载'
        }}
      >
        {
          this.state.tab === '1'
            ?
            <BarChart data={this.state.datas}
              height={this.props.height - 85}
              geomType={'intervalStack'}
              padding={[10, 40, 30, 50]}
              onFirstLoad={(chartIns) => { this.setState({ chartIns }) }}
              colors={["#3aa1ff", "#5ddfcf", "#4ecb73", "#fbd437", "#eaa674", "#8a7bd4", "#975fe5", "#749fea", "#36cbcb", "#74b1ea", "#5254cf", "#435188"]}
            />
            :
            <React.Fragment>
              <Table
                dataSource={this.state.tableData}
                columns={columns}
                bordered
                size="small"
                pagination={this.state.pagination}
              />
              <Table ref='table' columns={columns} dataSource={this.state.tableData} pagination={false} style={{ display: 'none' }} />
            </React.Fragment>
        }
      </MainCard>
    )
  }

  // componentDidMount() {
  //   this.getData()
  //   this.getPieData()
  // }

  componentWillReceiveProps(nextProps) {
    if (JSON.stringify(nextProps) !== JSON.stringify(this.props)) {
      setTimeout(() => {
        this.getData()

      }, 50)
    }
  }
}


