import React from "react";
import {Divider,Table} from 'antd'
import {postAction} from "../../../../axios";
import PieChart from '../../../../common/components/BizCharts/PieChart';
import MainCard from "@/common/components/MainCard";
import ReactDOM from "react-dom";
import {hostUrl} from "../../../../env";


const columns = [{
    title:'序号',
    dataIndex: 'sortIndex',
    width: '10%',
},{
    title: '不同级别',
    dataIndex: 'item',
    render: (text) => {
        return (
            <span title={text}>{text}</span>
        )
    }
}, {
    title: '人数',
    dataIndex: 'count'
}];

class Labelline extends React.Component {
  constructor(props) {
  	super(props);
    this.state = {
        tab: '1',
        chartIns: undefined,
        pieData: [
	      // {
	      //   item: "一级",
	      //   count: 40
	      // }
	    ],
        tableData: [
            // { key: '1', sortIndex:'1', college: '建筑学院', number: 165, rate: '5%' }
        ],
        pagination: {
            pageSize:5,
            showQuickJumper: true,
            total: null,
            showTotal: (total) => {
                return (
                    `共${total}条数据`
                )
            }
        }
    }
  }

  getPovertyLevel(){
    const {curYear,curCollege,curMajor,curGrade,curshengyuan}=this.props.filterValue
    postAction(
      '/bigdata/povertyAnalyze/povertyLevelNum',
      {
        "schoolYear":curYear,
        "collegeCode":curCollege,
        "majorCode":curMajor,
        "grade":curGrade,
        "bornSource":curshengyuan
      }
    ).then((res) => {
        if (res.success && res.obj.length > 0) {
            let newData = [];
            let tableData = [];
            res.obj.forEach(
                (item, index) => {
                    newData.push({
                        item: item.analyzeGroup,
                        count: item.analyzeValue
                    })
                    tableData.push({
                        key: (index + 1),
                        sortIndex: (index + 1),
                        item: item.analyzeGroup,
                        count: item.analyzeValue,
                    })
                })
            this.setState({
                pieData: newData,
                tableData,
                pagination: {
                    pageSize: 5,
                    showQuickJumper: true,
                    total: tableData.length,
                    showTotal: (total) => {
                        return (
                            `共${total}条数据`
                        )
                    }
                }
            })
        } else {
            this.setState({
                chartData: [],
                tableData: [],
                pagination: {
                    pageSize: 5,
                    showQuickJumper: true,
                    total: null,
                    showTotal: (total) => {
                        return (
                            `共${total}条数据`
                        )
                    }
                }
            })
        }
    }
    )
  }

    //导出明细
    getDetail () {
        const {curYear,curCollege,curMajor,curGrade,curshengyuan} = this.props.filterValue;
        window.location.href=`${hostUrl}/bigdata/povertyAnalyze/exportPovertyLevelNum?schoolYear=${curYear}&collegeCode=${curCollege}&majorCode=${curMajor}&grade=${curGrade}&bornSource=${curshengyuan}`;
    }

        //tab切换
        changeTab = (e) => {
            this.setState({ tab: e.target.value }, () => {
                if (this.refs['table']) {
                    const tableCon = ReactDOM.findDOMNode(this.refs['table'])
                    const table = tableCon.querySelector('table')
                    table.setAttribute('id', 'different-xls')
                }
            })
        }

  render() {

      const filterText = (
          <div className="filter-text" style={{top:20,left:0}}>{this.props.filterName.curYearName 
          ? 
          `${this.props.filterName.curYearName.substring(0,this.props.filterName.curYearName.length - 1)}学年-${Number((this.props.filterName.curYearName).substring(0,this.props.filterName.curYearName.length - 1)) + 1}学年`
           : 
           ""}
              <Divider type="vertical" />{this.props.filterName.curCollegeName}
              <Divider type="vertical" />{this.props.filterName.curMajorName}
              <Divider type="vertical" />{this.props.filterName.curGradeName}
              <Divider type="vertical" />{this.props.filterName.curshengyuanName}
          </div>
      );

    return (
        <MainCard
            className="main-card"
            title={<div className="common-title">不同级别人数
                <span>（单位：人）</span>
                {filterText}
            </div>}
            tab={this.state.tab}
            changeTab={this.changeTab}
            detailCallback={this.getDetail.bind(this)}
            downloadChart={{
                chartIns: this.state.chartIns,
                title: "不同级别人数"
            }}
            tableToExcel={{
                table: 'different-xls',
                filename: '不同级别人数excel下载'
            }}
        >
            {
                this.state.tab === '1'
                    ?
          <PieChart
              height={this.props.height - 70}
              data={this.state.pieData}
              padding={[10,30,40,60]}
              onFirstLoad={(chartIns) => {
                  this.setState({ chartIns })
              }} />
                    :
                    <React.Fragment>
                        <Table
                            dataSource={this.state.tableData}
                            columns={columns}
                            bordered
                            size="small"
                            style={{height:225}}
                            pagination={this.state.pagination}
                        />

                        <Table ref='table' columns={columns} dataSource={this.state.tableData} pagination={false} style={{ display: 'none' }} />
                    </React.Fragment>
            }

        </MainCard>
    );
  }

    componentDidMount () {
        this.getPovertyLevel()
    }

    componentWillReceiveProps (nextProps) {
        if (JSON.stringify(nextProps) !== JSON.stringify(this.props)) {
            setTimeout(() => {
                this.getPovertyLevel()
            }, 50)
        }
    }
}

export default Labelline;



