import React from "react";
import {Table} from 'antd';
import ReactDOM from 'react-dom';
import MainCard from "@/common/components/MainCard";
import ShowChart from './ShowChart';
import {postAction} from "../../../../axios";
import {hostUrl} from "../../../../env";


const columns = [
    {
      title:'序号',
      dataIndex: 'sortIndex',
      width: '6%',
    }, {
      title: '学院',
      dataIndex: 'college',
      render: (text) => {
          return (
              <span title={text}>{text}</span>
          )
      }
    }, {
      title: '金额达标率一级',
      dataIndex: 'moneylvOne'
    }, {
        title: '金额达标率二级',
        dataIndex: 'moneylvTwo'
    }, {
        title: '金额达标率三级',
        dataIndex: 'moneylvThree'
    }, {
        title:'人数达标率一级',
        dataIndex:'peopleNumOne'
    }, {
        title:'人数达标率二级',
        dataIndex:'peopleNumTwo'
    }, {
        title:'人数达标率三级',
        dataIndex:'peopleNumThree'
    }
];

class ZiZhuDaBiao extends React.Component {
  constructor(props) {
  	super(props);
    this.state = {
    	tab: '1',
    	chartIns: undefined,
    	total: 0,
    	barData: [
            // {
            //     "State":"机械工程学院",
            //     "金额达标率一级":31,
            //     "金额达标率二级":55,
            //     "金额达标率三级":25,
            //     "人数达标率一级":45,
            //     "人数达标率二级":12,
            //     "人数达标率三级":12,
            // }
	    ],
	    tableData:[
	    	// {
	    	// 	key: '1', 
	    	// 	sortIndex:'1', 
	    	// 	college: '建筑学院', 
	    	// 	moneylvOne: '60%',
        //   moneylvTwo: '50%',
        //   moneylvThree: '40%',
        //   peopleNumOne: '50%' ,
        //   peopleNumTwo: '67%',
        //   peopleNumThree: '90%'
	    	// }
	    ],
	    pagination: {
	        showQuickJumper: true,
	        total: null,
	        showTotal: (total) => {
	          return (
	            `共${total}条数据`
	          )
	        }
	    }
    }
  }

  getData () {
    const { curYear} = this.props.filterValue
    postAction(
        "/bigdata/povertyAnalyze/collegeSubsidizePercent",
        {
          schoolYear:curYear
        }
    ).then((res) => {
      if (res.success) {
        let newObj = {},newData = []
        let obj = 
        {
          "金额达标率一级":0,
          "金额达标率二级":0,
          "金额达标率三级":0,
          "人数达标率一级":0,
          "人数达标率二级":0,
          "人数达标率三级":0,
        }
        if(res.obj && res.obj.length > 0){
          res.obj.forEach(item => {
            if(newObj[item.collegeName]){
              if(item.povertyLevel === "一级"){
                newObj[item.collegeName] = {
                  ...obj,
                  ...newObj[item.collegeName],
                    "金额达标率一级":item.moneyPercent || 0,
                    "人数达标率一级":item.numPercent || 0,
                }
              }else if(item.povertyLevel === "二级"){
                newObj[item.collegeName] = {
                  ...obj,
                  ...newObj[item.collegeName],
                  "金额达标率二级":item.moneyPercent || 0,
                  "人数达标率二级":item.numPercent || 0,
                }
              }else if(item.povertyLevel === "三级"){
                newObj[item.collegeName] = {
                  ...obj,
                  ...newObj[item.collegeName],
                  "金额达标率三级":item.moneyPercent || 0,
                  "人数达标率三级":item.numPercent || 0,
                }
              }
            }else{
              if(item.povertyLevel === "一级"){
                newObj[item.collegeName] = {
                  ...obj,
                  "金额达标率一级":item.moneyPercent || 0,
                  "人数达标率一级":item.numPercent || 0,
                }
              }else if(item.povertyLevel === "二级"){
                newObj[item.collegeName] = {
                  ...obj,
                  "金额达标率二级":item.moneyPercent || 0,
                  "人数达标率二级":item.numPercent || 0,
                }
              }else if(item.povertyLevel === "三级"){
                newObj[item.collegeName] = {
                  ...obj,
                  "金额达标率三级":item.moneyPercent || 0,
                  "人数达标率三级":item.numPercent || 0,
                }
              }
            }
          })
          Object.keys(newObj).forEach(item => {
            let samllObj = {
              'State':item,
              ...newObj[item]
            }
            newData.push(samllObj)
          })
          let newTable = []
          if(newData && newData.length > 0){
            newData.forEach((item,index) => {
              newTable.push({
                key: index+1, 
                sortIndex:index+1, 
                college: item.State,
                moneylvOne: item.金额达标率一级+'%',
                moneylvTwo: item.金额达标率二级+'%',
                moneylvThree: item.金额达标率三级+'%',
                peopleNumOne: item.人数达标率一级+'%' ,
                peopleNumTwo: item.人数达标率二级+'%',
                peopleNumThree: item.人数达标率三级+'%'
              })
            })
          }
          this.setState({
            barData:newData,
            tableData:newTable,
            pagination: {
              pageSize:5,
              showQuickJumper: true,
              total: newTable.length,
              showTotal: (total) => {
                return (
                  `共${total}条数据`
                )
              }
            }
          })
        }else{
          this.setState({
            barData:[],
            tableData:[],
            pagination: {
              pageSize:5,
              showQuickJumper: true,
              total: null,
              showTotal: (total) => {
                return (
                  `共${total}条数据`
                )
              }
            }
          })
        }
        
      }
    })
  }

    //导出明细
    getDetail () {
        const {curYear} = this.props.filterValue;
        window.location.href=`${hostUrl}/bigdata/povertyAnalyze/exportCollegesubSidizePercent?schoolYear=${curYear}`;
    }

  //tab切换
  changeTab = (e) => {
    this.setState({ tab: e.target.value }, () => {
      if (this.refs['table']) {
        const tableCon = ReactDOM.findDOMNode(this.refs['table'])
        const table = tableCon.querySelector('table')
        table.setAttribute('id', 'zizhu-xls')
      }
    })
  }

  componentDidMount(){
    this.getData()
  }

  componentWillReceiveProps(nextProps) {
    if (JSON.stringify(nextProps) !== JSON.stringify(this.props)) {
      setTimeout(() => {
        this.getData()
      }, 50)
    }
  }

  render() {

    return (
	  <MainCard
        className="main-card"
        title={<div className="common-title">各学院资助达标率分布<span>(单位：%)</span>
        <div className="filter-text-sp">{this.props.filterName.curYearName 
          ? 
          `${this.props.filterName.curYearName.substring(0,this.props.filterName.curYearName.length - 1)}学年-${Number((this.props.filterName.curYearName).substring(0,this.props.filterName.curYearName.length - 1)) + 1}学年`
          : ""}
    </div>
        </div>}
        tab={this.state.tab}
        changeTab={this.changeTab}
        // detailCallback={this.mingxi}
        downloadChart={{
          chartIns: this.state.chartIns,
          title: "各学院资助达标率分布"
        }}
        tableToExcel={{
          table: 'zizhu-xls',
          filename: '各学院资助达标率分布excel下载'
        }}
        detailCallback={this.getDetail.bind(this)}
      >
        {
          this.state.tab === '1'
            ?
              <ShowChart
              geomType={'intervalStack'}
                data={this.state.barData}
                height={this.props.height - 80}
                onFirstLoad={(chartIns) => { this.setState({ chartIns }) }}
              />
            :
              <React.Fragment>
                <Table
                  dataSource={this.state.tableData}
                  columns={columns}
                  bordered
                  size="middle"
                  pagination={this.state.pagination}
                />
                <Table ref='table' columns={columns} dataSource={this.state.tableData} pagination={false} style={{ display: 'none' }} />
              </React.Fragment>
        }
        
      </MainCard>
    )
  }
}

export default ZiZhuDaBiao



