import React from 'react'
import { Divider, Table } from 'antd'
import ReactDOM from 'react-dom'
import MainCard from "@/common/components/MainCard"
import PieChart from '@/common/components/BizCharts/PieChart'
import { postAction } from "../../../../axios"
import {hostUrl} from "../../../../env"


const columns = [{
    title: '序号',
    dataIndex: 'sortIndex',
    width: '10%',
}, {
    title: '地点',
    dataIndex: 'address',
    render: (text) => {
        return (
            <span title={text}>{text}</span>
        )
    }
}, {
    title: '人数',
    dataIndex: 'num'
}];
export default class PoolStudentStudy extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            tab: '1',
            chartIns: undefined,
            newPieData: [
                // { item: '事例一', count: 40 }
            ],
            tableData: [
                // { key: '1', sortIndex:'1', address: '建筑学院', num: 165}
            ],
            total: 0,
            pagination: {
                showQuickJumper: true,
                total: null,
                showTotal: (total) => {
                    return (
                        `共${total}条数据`
                    )
                }
            }
        }
        this.getData = this.getData.bind(this);
    }

    getData() {
        const { curYear, curCollege, curMajor, curGrade, curshengyuan } = this.props.filterValue
        postAction(
            "/bigdata/povertyAnalyze/povertyStudentEmployment",
            {
                "schoolYear": curYear,
                "collegeCode": curCollege,
                "majorCode": curMajor,
                "grade": curGrade,
                "bornSource": curshengyuan
            }
        ).then((res) => {
            if (res.success) {
                let newData = [], newTable = [];
                res.obj && res.obj.forEach((item, index) => {
                    newData.push({
                        item: item.cityName,
                        count: Number(item.peopleNum)
                    })
                    newTable.push({
                        key: (index + 1),
                        sortIndex: (index + 1),
                        address: item.cityName,
                        num: item.peopleNum
                    })
                })
                this.setState({
                    newPieData: newData,
                    tableData: newTable,
                    pagination: {
                        showQuickJumper: true,
                        total: newTable.length,
                        showTotal: (total) => {
                            return (
                                `共${total}条数据`
                            )
                        }
                    }
                })
            } else {
                this.setState({
                    newPieData: [],
                    tableData: []
                })
            }
        })
    }

    //tab切换
    changeTab = (e) => {
        this.setState({ tab: e.target.value }, () => {
            if (this.refs['table']) {
                const tableCon = ReactDOM.findDOMNode(this.refs['table'])
                const table = tableCon.querySelector('table')
                table.setAttribute('id', 'top5-xls')
            }
        })
    }

    //导出明细
    getDetail() {
        const { curYear, curCollege, curMajor, curGrade, curshengyuan } = this.props.filterValue;
        window.location.href = `${hostUrl}/bigdata/povertyAnalyze/exportPovertyStudentEmployment?schoolYear=${curYear}&collegeCode=${curCollege}&majorCode=${curMajor}&grade=${curGrade}&bornSource=${curshengyuan}`;
    }

    render() {
        return (
            <MainCard
                title={
                    <div className="common-title">家庭经济困难学生就业去向TOP5<span>（单位：人）</span>
                        <div className="filter-text" style={{ left: 0, top: 18 }}>{this.props.filterName.curYearName 
                            ? 
                            `${this.props.filterName.curYearName.substring(0,this.props.filterName.curYearName.length - 1)}学年-${Number((this.props.filterName.curYearName).substring(0,this.props.filterName.curYearName.length - 1)) + 1}学年`
                            : ""}
                            <Divider type="vertical" />{this.props.filterName.curCollegeName}
                            <Divider type="vertical" />{this.props.filterName.curMajorName}
                            <Divider type="vertical" />{this.props.filterName.curGradeName}
                            <Divider type="vertical" />{this.props.filterName.curshengyuanName}
                        </div>
                    </div>}
                tab={this.state.tab}
                changeTab={this.changeTab}
                detailCallback={this.getDetail.bind(this)}
                downloadChart={{
                    chartIns: this.state.chartIns,
                    title: "家庭经济困难学生就业去向TOP5"
                }}
                tableToExcel={{
                    table: 'top5-xls',
                    filename: '家庭经济困难学生就业去向TOP5excel下载'
                }}
            >
                {
                    this.state.tab === '1'
                        ?
                        <PieChart
                            data={this.state.newPieData}
                            innerRadius={0.55}
                            height={this.props.height - 80}
                            padding={[0,20,40,20]}
                            onFirstLoad={(chartIns) => { this.setState({ chartIns }) }}
                            colors={["#3aa1ff", "#5ddfcf", "#4ecb73", "#fbd437", "#eaa674", "#8a7bd4", "#975fe5", "#749fea", "#36cbcb", "#74b1ea", "#5254cf", "#435188"]}
                        />
                        :
                        <React.Fragment>
                            <Table
                                dataSource={this.state.tableData}
                                columns={columns}
                                bordered
                                size="small"
                            />

                            <Table ref='table' columns={columns} dataSource={this.state.tableData} pagination={false} style={{ display: 'none' }} />
                        </React.Fragment>
                }
            </MainCard>
        )
    }

    componentDidMount() {
        this.getData()
    }

    componentWillReceiveProps(nextProps) {
        if (JSON.stringify(nextProps) !== JSON.stringify(this.props)) {
            setTimeout(() => {
                this.getData()
            }, 50)
        }
    }
}


